  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.popup {
	display: grid;
	grid-template-columns: repeat(4, 1fr) 1fr;
	grid-template-rows: 0.20fr repeat(3, 1fr) 0.20fr;
	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
	margin-bottom: 10px;
	height:  95vh;
	border-radius: 5px;
	background-color: #FEFEFF;
	box-shadow:0 0 0 1600px rgba(0,0,0,0.90);
}

.header { 
	font-weight: bold;
	font-size: 15px;
	padding-left: 20px;
	padding-top: 5px;
	grid-area: 1 / 1 / 2 / 6; 

}
.info { 
	padding-left: 20px;
	padding-top: 5px;
	grid-area: 2 / 5 / 5 / 6; 
	background-color: #f0fbff;
	box-shadow: inset 0 7px 9px -7px rgba(0,0,0,0.4);
	overflow: auto;
}



.form { 
	grid-area: 2 / 1 / 5 / 5; 
	box-shadow: inset 0 7px 9px -7px rgba(0,0,0,0.4);
	padding-left: 20px;
	padding-top: 5px;
	overflow:  auto;
}
.submit { 
	grid-area: 5 / 1 / 6 / 6; 
	box-shadow: 0px -7px 9px -7px rgba(0,0,0,0.4);
}

.title{
	font-weight: 500;
	font-size: 15px;
}

.message{
	width:  800px;
	background-color: #f0fbff;
	margin-top: 20px;
	margin-left: 60px;
	border-radius: 5px;
	padding:  5px;
	font-size: 13px;
	font-weight: 400;


}

.info-key{
	max-height: 5px;
	font-size: 13px;
	font-weight: bold;
}
.info-key-name{
	max-height: 10px;
	margin-left: 3px;
	font-size: 13px;
	font-weight: 400;
}

.button-submit{
